// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import axios from 'axios';//Ajax
import VueAxios from 'vue-axios';//Vue
import Loading from 'vue-loading-overlay';//loading
import 'vue-loading-overlay/dist/vue-loading.css';//loadingcss
import 'bootstrap'; //bootstrap  jQuery  popper

import App from './App';
import router from './router';
import './bus';
import currencyFilter from './filters/currency';
import dateFilter from './filters/date';
//vee-validate
import VeeValidate from 'vee-validate'
import zhTW from 'vee-validate/dist/locale/zh_TW'
import VueI18n from 'vue-i18n'


Vue.use(VueI18n)

const i18n = new VueI18n({
  locale: 'zhTW'
})
Vue.use(VeeValidate, {
  events: 'input|blur', //
  i18n,
  dictionary: {
    zhTW
  }
})

Vue.filter('date', dateFilter);

Vue.config.productionTip = false
Vue.use(VueAxios, axios) //  Vue (main.js)Vue.use
Vue.filter('currency', currencyFilter);
//Vue.use(VeeValidate);
axios.defaults.withCredentials = true;//APIsectionCookie  Cookie


Vue.component('Loading', Loading);//
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
});

router.beforeEach((to, from, next) => {//router.beforeEach()
  console.log('to', to, 'from', from, 'next', next);
  // ...
  if (to.meta.requiresAuth) {//login
    const api =`${process.env.APIPATH}/api/user/check`;//'https://vue-course-api.hexschool.io/api/magic/products';
    axios.post(api).then((response) => {
    console.log(response.data);
    if (response.data.success) {
       next();
    } else {
       next({
         path: '/login',
       });
    }
 });
  } else {
    next(); //
  }
});

//test

new Vue({
  i18n,
  el: '#app',
  components: { App },
  template: '<App/>',
  router,
})


